/**
 * Utilitaires API partagés
 * ⚠️ DEPRECATED: Utiliser lib/api/client.ts (apiClient, apiGet, apiPost, etc.) pour timeouts et meilleure gestion erreurs
 */
export const API_URL = (process.env.NEXT_PUBLIC_API_URL || 'http://localhost:3001/api').replace(/\/$/, '');

// Vérifier que l'URL est correcte en dev
if (typeof window !== 'undefined' && process.env.NODE_ENV === 'development') {
  console.log('[API] URL configurée:', API_URL);
}

/**
 * @deprecated Utiliser apiClient/apiGet/apiPost depuis lib/api/client.ts pour timeouts et meilleure gestion erreurs
 */
export async function fetchAPI<T>(endpoint: string, options?: RequestInit & { timeout?: number }): Promise<T> {
  const { timeout = 30000, ...restOptions } = options || {};
  const controller = new AbortController();
  const timeoutId = setTimeout(() => controller.abort(), timeout);

  try {
    const response = await fetch(`${API_URL}${endpoint}`, {
      ...restOptions,
      signal: controller.signal,
      headers: {
        'Content-Type': 'application/json',
        ...restOptions?.headers,
      },
      credentials: 'include',
    });

    clearTimeout(timeoutId);

    if (!response.ok) {
      const errorData = await response.json().catch(() => ({
        message: `API Error: ${response.status} ${response.statusText}`
      }));
      throw new Error(errorData.message || errorData.error || `API Error: ${response.status}`);
    }

    return response.json();
  } catch (error: any) {
    clearTimeout(timeoutId);

    // Gérer timeout
    if (error.name === 'AbortError' || controller.signal.aborted) {
      throw new Error(
        `La requête a pris trop de temps (${Math.round(timeout / 1000)}s). Veuillez réessayer.`
      );
    }

    // Améliorer le message d'erreur pour "Failed to fetch"
    if (error.message === 'Failed to fetch' || error.name === 'TypeError') {
      throw new Error(`Impossible de se connecter au serveur. Vérifiez votre connexion internet ou que le backend est démarré.`);
    }
    throw error;
  }
}
